import React, { Fragment } from "react";
import expertise from "../../styles/assets/images/Webblog/expertise.png";
import timeIsMoney from "../../styles/assets/images/Webblog/time-is-money.png";
import improvement from "../../styles/assets/images/Webblog/improvement.png";

import webDevExpertise from "../../styles/assets/images/Webblog/webdev1.jpg";
import webDevTimeManage from "../../styles/assets/images/Webblog/webdev2.jpg";
import webDevImprovement from "../../styles/assets/images/Webblog/webdev3.jpg";
import { useState } from "react";
import { Col, Nav, Row, Tab } from "react-bootstrap";

const pocData = [
  {
    img: expertise,
    title: "EXPERTISE AND EXPERIENCE",
    description:
      "Web development is a complex field that requires a wide  range of skills, from coding to design to user experience. A  professional web development team will have the expertise and experience necessary to build a high-quality website that meets your business needs.",
    eventKey: "expertiseAndExperience",
  },
  {
    img: timeIsMoney,
    title: "TIME SAVING",
    description:
      " Building and maintaining a website can be time-consuming,especially if you don't have the technical skills or knowledge. By outsourcing web development to a professional  team, you can save time and focus on running your business.",
    eventKey: "timeIsMoney",
  },
  {
    img: improvement,
    title: "IMPROVED USER EXPERIENCE",
    description:
      "A professional web development team can design and build a  website that is user-friendly, easy to navigate, and  visually appealing. This can help to improve the user experience and encourage visitors to stay on your site longer, increasing the chances of conversion.",
    eventKey: "improvementAndUserExp",
  },
];

const forPocImg = [
  {
    img: webDevExpertise,
    alt: "experience-img",
    eventKey: "expertiseAndExperience",
  },
  {
    img: webDevTimeManage,
    alt: "time-management-img",
    eventKey: "timeIsMoney",
  },
  {
    img: webDevImprovement,
    alt: "improvement-img",
    eventKey: "improvementAndUserExp",
  },
];

const WebDevelopment = () => {
  // const [selectedIdx, setSelectedIdx] = useState(0);
  const [key, setKey] = useState("expertiseAndExperience");

  // const handleMouseSelect = (ele, index) => {
  //   const idx = forPocImg?.findIndex((e, i) => i === index);
  //   setSelectedIdx(idx);
  // };

  return (
    <Fragment>
      <div className="business-wrap my-5 pt-5">
        <div className="container-fluid">
          <div className="business-need p-5 row">
            <div className="col-12">
              <div className="mx-5">
                <h3 className="mb-2 fw-bold">
                  Why Your Business Needs A Professional Web Development Team
                </h3>
                <p className="text-dark fw-semibold fs-3 mt-4">
                  In today's digital age, having a strong online presence is
                  crucial for any business. A well-designed and user-friendly
                  website can help to attract and retain customers, and can even
                  be a key driver of revenue. However, building and maintaining
                  a website is no easy task. That's where a professional web
                  development team comes in.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
        <div class="business">
          <div class="text-center justify-content-center d-flex row">
            <div class="col-md-7">
              <h4 class="fw-semibold text-dark my-5">
                Why should you invest in a professional web development team for
                your business? Here are just a few reasons
              </h4>
            </div>
          </div>
        </div>
        <Tab.Container activeKey={key}>
          <Row className="d-flex align-items-center">
            <Col className="col-md-6 col-lg-6 col-sm-8 col-12 d-none d-md-block">
              <Tab.Content>
                {forPocImg.map((ele, ind) => (
                  <Tab.Pane key={ind} eventKey={ele?.eventKey}>
                    <div>
                      <img
                        className="d-flex m-auto d-flex justify-content-center shadow rounded w-75"
                        src={ele.img}
                        alt="poc"
                      />
                    </div>
                  </Tab.Pane>
                ))}
              </Tab.Content>
            </Col>
            <Col className="col-md-6 col-lg-6 col-sm-8 col-12">
              <Row>
                <Col md={12}>
                  <div className="webdev-head ">
                    <h2 className="text-capitalize titlecard fs-1 font-weight-normal">
                      POC and Open Source Development Services
                    </h2>
                  </div>
                </Col>
              </Row>
              <Row className="webdev-nav-link">
                <Col md={12}>
                  <Nav variant="pills" className="flex-column">
                    {pocData.map((ele, ind) => {
                      const { img, description, title, eventKey } = ele;
                      return (
                        <Nav.Item
                          key={ind}
                          onMouseEnter={() => setKey(eventKey)}
                        >
                          <Nav.Link eventKey={eventKey}>
                            <img alt="poc" src={img} />
                            {title}
                          </Nav.Link>
                          <p>{description}</p>
                        </Nav.Item>
                      );
                    })}
                  </Nav>
                </Col>
              </Row>
            </Col>
          </Row>
        </Tab.Container>
        <div class="row">
          <h3 class="titlecard fw-bold">Conclusion</h3>
          <p class="fs-3 fw-semibold">
            In conclusion, investing in a professional web development team can
            be a smart decision for any business looking to improve their online
            presence. Whether you need a new website or an update to an existing
            one, a professional team can provide the expertise and experience
            necessary to create a high-quality, user-friendly site that drives
            results for your business.
          </p>
        </div>
      </div>
    </Fragment>
  );
};

export default WebDevelopment;
